import React, { Component } from 'react'
import MdEditor from './mdEditor'
import Classify from './classify'
import {Button, Input, Form} from 'antd'
import style from './index.module.css'
class EditQuestions extends Component {
    static defaultProps = {
        getTitleVal:()=>{}, // 获取标题输入的内容
        getQuestionsStem:()=>{}, // 获取题干输入的内容
        getQuestionsAnswer:()=>{}, // 获取答案输入的内容
        submitClick:()=>{}, // 点击提交按钮后，的回到函数
        stemMd:'', // 题干的默认内容
        answerMd:'', // 答案的默认内容
        title:'' // 标题的默认内容
    }
    render() {
        let { 
            getTitleVal, 
            submitClick,
            getQuestionsAnswer,
            getQuestionsStem,
            title,
            stemMd,
            answerMd 
        } = this.props;
        return (
            <div>
                
                <Form layout="vertical">
                    <h3>题目信息</h3>
                    <Form.Item label="题干">
                        <Input
                            onChange={ev => getTitleVal(ev.target.value)}
                            value={title}
                            className={style.titleInput}
                            size="large"
                            placeholder="请输入题目标题，不超过20个字" 
                        />
                    </Form.Item>
                    <Form.Item label="题目主题" >
                        <MdEditor editorId="questionsStem" editMd={stemMd} editorChange={mdEditor => getQuestionsStem(mdEditor)} />
                    </Form.Item>

                    <Classify />
                    
                    <h3>答案信息</h3>
                    <Form.Item>
                        <MdEditor editorId="questionsAnswer" editMd={answerMd} editorChange={mdEditor => getQuestionsAnswer(mdEditor)} />
                    </Form.Item>
                    
                </Form>
                
                <div className={style.footBtn}>
                    <Button type="primary" size="large" onClick={() => submitClick()}>提交</Button>
                </div>
            </div>
        )
    }
}

export default EditQuestions;