import React, { Component } from 'react'
import {Modal, message} from 'antd'
import {connect} from 'dva'

import BaseContent from '@/components/BaseContent'
import EditQuestions from '@/components/EditQuestions'

const {confirm} = Modal;

let mapState = store=>{
  window.store = store;
  return {...store.addQuestions,...store.main}
}
let mapDispatch = dispatch => {
  return {
    logQuestions(payload){
      dispatch({type:'addQuestions/initData',...payload});
    },
    addQuestions(payload){
      dispatch({type:'addQuestions/addQuestions',...payload})
    }
  }
}
@connect(mapState,mapDispatch)
class AddQuestions extends Component {
  state = {}
  static getDerivedStateFromProps(nextProps,nowState){
    if(nextProps.addStatus === 1){
      Modal.success({
        content:'试题添加成功',
        onOk:()=>nextProps.logQuestions({addStatus:0})
      });
    }else if(nextProps.addStatus === 3){
      Modal.error({
        content:'试题添加失败',
        onOk:()=>nextProps.logQuestions({addStatus:0})
      });
    }
    return{
      ...nowState,...nextProps
    }
  }
  addQuestions = ()=>{
    confirm({
      title: '你确定要添加这道试题吗?',
      content: '真的要添加吗？',
      onOk:()=>{
        let {title,questions_answer,questions_stem,questions_type_id,exam_id,subject_id} = this.props;
        let {user_id} = this.props.userInfo
        if(!user_id){
          message.error('没有用户信息',1);
          return;
        }
        this.props.addQuestions({
          questions_answer,
          questions_stem,
          questions_type_id,
          exam_id,subject_id,
          user_id,
          title
        });
      },
      onCancel() {},
    });
  }
  componentDidMount(){
    localStorage.removeItem('editQuestionsInfo');
  }
  render() {
    let {
      title,
      questions_answer,
      questions_stem
    } = this.props;
    return <BaseContent>
    <EditQuestions
      getTitleVal={title=>this.props.logQuestions({title})}
      getQuestionsAnswer={questions_answer=>this.props.logQuestions({questions_answer})}
      getQuestionsStem={questions_stem=>this.props.logQuestions({questions_stem})}
      submitClick={()=>this.addQuestions()}
      title={title}
      stemMd={questions_stem}
      answerMd={questions_answer}
    />
    </BaseContent>
  }
}
export default AddQuestions;
